<template>
    <div class="index-home">
        <van-search
            shape="round"
            background="#4fc08d"
            disabled
            placeholder="请输入搜索关键词"
            @click.stop="handleSearchTab"
        />
        <van-tabs
            v-model="active1"
            swipeable
            animated
            @click="setTabData"
            color="#4fc08d"
            title-active-color="#4fc08d"
        >
            <van-tab
                :title="item.title"
                :name="item.name"
                :to="'/index?tab=' + item.name"
                v-for="(item, index) in $store.state.tab.indexTab"
                :key="index"
            >
                <div
                    @scroll="scroll"
                    :ref="item.name"
                    :class="['swiper-item', item.name]"
                >
                    <IndexBlogItem
                        :middleBlogList="$store.state.blog.indexBlogs"
                        :handleView="handleView"
                    />
                </div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
export default {
    props: {
        active: {
            type: String
        }
    },
    data() {
        return {};
    },
    mounted() {
        this.$store.dispatch("getIndexPageBlog", {});
        this.setScroll();
    },
    computed: {
        active1() {
            return this.active === ""
                ? this.$store.state.tab.indexTab.reduce((pre, cur) =>
                      pre.isActive ? pre.name : cur.name
                  )
                : this.active;
        }
    },
    methods: {
        handleView(id) {
            this.$router.push("/show-blog/" + id);
        },
        handleSearchTab() {
            this.$router.push("/index?key=IndexNavSearch");
        },
        setTabData(name) {
            this.$store.dispatch("setIndexTab", name);
        },
        scroll(e) {
            this.$store.dispatch("setIndexTabScroll", {
                name: this.active,
                scrollHeight: e.target.scrollTop
            });
        },
        setScroll() {
            this.$nextTick(function() {
                for (const key in this.$refs) {
                    let h = this.$store.state.tab.indexTab.reduce((pre, cur) =>
                        pre.name === key ? pre.oldScroll : cur.oldScroll
                    );
                    this.$refs[key][0].scrollTo(0, h, { behavior: "smooth" });
                }
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.index-home {
    width: 100%;
    height: 100%;
    .box {
        width: 100%;
        height: 1rem;
        background-color: red;
    }
    .swiper-item {
        width: 100%;
        height: calc(100vh - 148px);
        overflow-y: auto;
        font-size: 18px;
    }
    .hot {
    }

    .new {
    }
}
</style>